/**
 * For those times you want to use <ul>s and <li>s without those damn dots.
 */
.clearfix:after {
  content: "";
  display: table;
  clear: both; }

/**
 * For markup you want to hide but still keep accessible for screen readers.
 */
.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px); }

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html {
  font-family: "mobile"; }
  @media (min-width: 768px) {
    html {
      font-family: "tablet"; } }
  @media (min-width: 1224px) {
    html {
      font-family: "desktop"; } }

