.btn-primary {
  display: inline-block;
  padding: 10px 15px;
  font-size: 18px;
  line-height: 1;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
  transition: background .45s; }
  .btn-primary:hover {
    text-decoration: none;
    color: #F8B853; }

h1 {
  color: white; }

.flip {
  transform: perspective(600px) rotateY(0deg);
  transition: transform .3s ease-in; }
  .flip--fast {
    transition: transform .1s ease-in; }
  .flip--slow {
    transition: transform 3s ease-in; }
  .flip.flipped {
    transform: perspective(600px) rotateY(180deg); }

.jack {
  width: 100px;
  height: 100px; }

.msg-positive, .msg-neutral, .msg-negative {
  clear: both;
  display: block;
  margin: 5px 0 15px 0;
  padding: 10px; }

.msg-positive {
  color: #089b58;
  background: #e2fbf0; }

.msg-neutral {
  color: #666600;
  background: #ffffcc; }

.msg-negative {
  color: #b30006;
  background: #ffeedd; }

.skeletor-component {
  transform: perspective(600px) rotateY(0deg);
  transition: transform .3s ease-in; }
  .skeletor-component.flipped {
    transform: perspective(600px) rotateY(180deg); }

